<template lang="html">
  <div class="demo-field-select">
    <field-select label="标题" :options="options" @change-value="callChangeValue"></field-select>
    <field-select label="标题" :options="options" :selected-option="selectedOption" @change-value="callChangeValue"></field-select>
    <field-select label="标题" :options="options" placeholder="自定义默认文本" @change-value="callChangeValue"></field-select>
    <field-select label="标题" :options="options" :selected-option="selectedOption" disabled @change-value="callChangeValue"></field-select>
  </div>
</template>

<script>
import FieldSelect from 'components/field/select.vue'

export default {
  name: 'demo-field-select',
  components: {
    FieldSelect
  },
  data () {
    return {
      options: [
        { value: 1, text: 'a' },
        { value: 2, text: 'b' },
        { value: 3, text: 'c' },
        { value: 4, text: 'd' },
        { value: 5, text: 'e' },
        { value: 6, text: 'ffffffff' }
      ],
      selectedOption: {
        value: 3,
        text: 'c'
      }
    }
  },
  methods: {
    callChangeValue (option, unique) {
      console.log(option, unique)
    }
  }
}
</script>

<style lang="pcss">
@namespace demo {
  @b field-select {

  }
}
</style>



