<template lang="html">
  <div class="hd-select" @click.stop="switchOption">
    <label class="hd-select-label" v-text="label"></label>
    <div class="hd-select-pane">
      <div class="hd-select-core" v-text="text"></div>
      <svg-down size="0.3"></svg-down>
      <transition name="hd-select-line-fade">
        <div v-show="open" class="hd-select-line"></div>
      </transition>
      <ul class="hd-select-options" v-show="open">
        <li v-for="(option, index) in options" :key="index" :class="{ 'is-selected': option.value === model.value }" v-text="option.text" @click="setValue(option)"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import SvgDown from 'components/svg/down.vue'

export default {
  name: 'hd-select',
  components: {
    SvgDown
  },
  props: {
    unique: String,
    label: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    disabled: Boolean,
    options: {
      type: Array,
      default () {
        return []
      }
    },
    selectedOption: {
      type: Object,
      default () {
        return null
      }
    }
  },
  data () {
    return {
      open: false,
      model: this.selectedOption || {}
    }
  },
  computed: {
    text () {
      return this.model.text ? this.model.text : this.placeholder
    },
    evt () {
      let evt = document.createEvent('HTMLEvents')
      evt.initEvent('hd_select', false, true)
      return evt
    }
  },
  methods: {
    switchOption () {
      if (!this.disabled) {
        if (!this.open) {
          document.dispatchEvent(this.evt)
        }
        this.open = !this.open
      }
    },
    hideOption () {
      if (this.open) {
        this.open = false
      }
    },
    setValue (option) {
      this.model = option
      this.$emit('change-value', option, this.unique)
    }
  },
  mounted () {
    document.addEventListener('click', this.hideOption, false)
    document.addEventListener('hd_select', this.hideOption, false)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.hideOption)
    document.removeEventListener('hd_select', this.hideOption)
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b select {
    display: flex;
    align-items: center;
    font-size: 32px;
    padding: 0 20px;
    line-height: 1.5;
    border-bottom: 1px solid #d9d9d9;
    background-color: #fff;

    @e label {
      width: 160px;
      flex-shrink: 0;
      color: #666;
      padding: 25px 0;
    }

    @e core {
      flex-grow: 1;
    }

    @e pane {
      display: flex;
      align-items: center;
      flex-grow: 1;
      padding: 25px 0;
      position: relative;
    }

    @e line {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: #ff5252;

      @e fade {
        @e enter {
          transform: scaleX(0);

          @e active {
            transition: transform .2s;
          }
        }

        @e leave {
          @e active {
            transform: scaleX(0);
            transition: transform .2s;
          }
        }
      }
    }

    @e options {
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      background-color: #fff;
      box-shadow: 0 3px 6px rgba(0, 0, 0, .16),
                  0 3px 6px rgba(0, 0, 0, .23);
      max-height: 400px;
      overflow-y: auto;
      cursor: pointer;
      z-index: 900;

      li {
        padding: 16px 32px;

        @when selected {
          background-color: #2db7f5;
          color: #fff;
        }

        &:hover {
          background-color: #eee;
        }
      }
    }
  }
}
</style>



